                Edit Label
                </v-card-title>
                <div style="width: 100%; padding: 0% 7% 0% 7%;">
                    <div class="d-flex justify-center align-center">
                        <v-text-field prepend-icon="mdi-plus" label="Add label" v-model="addLabel"></v-text-field>
                        <v-btn fab icon small @click="onAddLabelClick"><v-icon>mdi-check</v-icon></v-btn>
                    </div>
                    <div class="d-flex align-center py-2" v-for="(label, i) in labels" :key="i">
                        <span v-if="labelEditMode === true && editedIndex == i" class="pr-4" color="red"><v-btn icon @click="labelEditMode = false"><v-icon >mdi-close</v-icon></v-btn></span>
                        <span v-else class="pr-4"><v-icon>mdi-label</v-icon></span>
                        
                        <v-text-field v-if="labelEditMode === true && editedIndex == i" v-model="editLabelValue"></v-text-field>
                        <span v-else>{{ label }}</span>
                        <div class="ml-auto">
                            <v-btn small fab icon @click="btnDeleteLabel(label, i)"><v-icon>mdi-trash-can</v-icon></v-btn>
                            <v-btn v-if="labelEditMode === true && editedIndex == i" small fab icon @click="updateLabel(i)"><v-icon>mdi-check</v-icon></v-btn>
                            <v-btn v-else small fab icon @click="labelToggler(label, i)"><v-icon>mdi-pencil</v-icon></v-btn>
                        </div>
                    </div>
                </div>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    text
                    @click="labelDialog = false"
                >
                    Done
                </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>


        <!-- addLabel Dialog -->
        <v-dialog
            v-model="addLabelDialog"
            persistent
            max-width="300"
            >
            <v-card>
                <v-card-title>
                Add label
                </v-card-title>
                <div style="width: 100%; padding: 0% 7% 0% 7%;">
                    <div v-for="(label, index) in labels" :key="index">
                        <v-checkbox
                        v-model="selectedLabel"
                        :label="label"
                        :value="label"
                        class="shrink mr-0 mt-0"
                        >
                        </v-checkbox>
                    </div>
                </div>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    text
                    color="red"
                    @click="addLabelDialog = false"
                >
                    Close
                </v-btn>
                <v-btn
                    text
                    @click="btnUpdateExistingTag"
                >
                    Update
                </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
        <!-- end add label dialog -->
        

        <!-- confirm delete dialog  -->
        <v-dialog
            v-model="confirmDeleteDialog"
            persistent
            max-width="350"
            >
            <v-card>
                <v-card-title class="headline">
                Delete this note?
                </v-card-title>
                <v-card-text>Deleted note will on trash for 20 days.</v-card-text>
                <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                    text
                    @click="confirmDeleteDialog = false"
                >
                    Close
                </v-btn>
                <v-btn
                    color="red darken-1"
                    text
                    @click="btnConfirmSingleDelete"
                >
                    Okay
                </v-btn>
                </v-card-actions>
            </v-card>
            </v-dialog>


            <!-- snack bar  -->
            <v-snackbar
            v-model="snackbar"
            >
            {{ msg }}

            <template v-slot:action="{ attrs }">
                <v-btn
                color="pink"
                text
                v-bind="attrs"
                @click="snackbar = false"
                >
                Close
                </v-btn>
            </template>
            </v-snackbar>
            <ColorPickerDialog :note_colorID="note_colorID" :note_color="note_color" :visible="showColorPickerDialog" @close="showColorPickerDialog=false"
            @success="onSuccessNoteColor"
            @error="onErrorNoteColor"
            />
            <EditNoteDialog 
            :updateSelectedNote="updateSelectedNote" 
            :toggleEditDialog="showEditDialog" 
            @closeEditModal="showEditDialog = false"
            @updateTitle="updateSelectedNote.title = $event"
            @updateContent="updateSelectedNote.content = $event"
            @updateSuccess="updated"
            @updateError="errorUpdating"
            />
            <!-- <BulkDeleteDialog
            :ids="ids"
            :bulkDialog="bulkDialog"
            @show="bulkDialog = false"
            /> -->
            <BulkDeleteDialog 
            :ids="ids" 
            :bulkDialog="bulkDialog" 
            @show="bulkDialog=false" 
            @onSuccessBulkDelete="successBulk"
            @onErrorBulkDelete="errorBulk" />

            <v-slide-x-reverse-transition>
                <div class="floating" v-show="selectionActive == true">
                
                    <v-tooltip top>
                        <template v-slot:activator="{ on }">
                            <v-btn @click="selectAll" class="mx-1" color="grey lighten-3" fab small dark v-on="on"><v-icon color="black">mdi-check</v-icon></v-btn>
                        </template>
                        <span>Select All</span>
                    </v-tooltip>
                    <v-tooltip top>
                        <template v-slot:activator="{ on }">
                            <v-btn @click="deselectAll" class="mx-1" color="grey lighten-3" fab small dark v-on="on"><v-icon color="black">mdi-close</v-icon></v-btn>
                        </template>
                        <span>Cancel</span>
                    </v-tooltip>
                    <v-btn
                        class="mx-1"
                        color="red"
                        dark
                        fab
                        @click="bulkDelete"
                    >
                        <v-icon>mdi-trash-can-outline</v-icon>
                    </v-btn>
                </div>
            </v-slide-x-reverse-transition>

    </v-app>
</template>
<script>
import Archive from '../components/Archive.vue'
import ColorPickerDialog from '../components/ColorPickerDialog.vue'
import EditNoteDialog from '../components/EditNoteDialog.vue'
import Loader from '../components/Loader.vue'
import BulkDeleteDialog from '../components/BulkDeleteDialog.vue'
export default {
    name: 'Home',
    components: {
        Archive, ColorPickerDialog, EditNoteDialog,
        Loader, BulkDeleteDialog
    },
    data: () => ({
        selectionActive: false,
        isLoading: false,
        showColorPickerDialog: false,
        showEditDialog: false,
        drawer: true,
        dialog: false,
        sesh_err: '',
        model: 0,
        snackbar: false,   
        msg: '',
        typingMode: false, 
        form: {},
        selected_noteId: '',
        selected_index: -1,

        // labels corner 
        labelDialog: false,
        labelEditMode: false,
        editedIndex: -1,
        labels: [],
        addLabel: '',
        myNotes: [],
        addLabelDialog: false,
        selectedLabel: [],
        oldLabelValue: '',
        editLabelValue: '',

        //user
        userInfo: {},
        // others
        darkModeSwitch: false,
        closeOnContentClick: false,

        // view
        listView: false,

        //notes
        delSelectedNoteIndex: -1,
        delSelectedNoteId: null,
        delSelectedNote: {},
        updateSelectedNote: "",
        confirmDeleteDialog: false,
        newNote: {
            title: '',
            content: '',
            tags: []
        },
        c_tag: [],
        tempNotes: [],
        filteredNotes: [],
        filtering: false,
        archiveStatus: false,
        myArchiveNotes: [],
        note_color: '',
        note_colorID: '',
        colorEdit_selectedNote: {},
        ids: [],
        bulkDialog: false
    
    }),
    
    methods: {
        deselectAll(){
            this.ids = []
            for(let i = 0; i < this.myNotes.length; i++){
                this.myNotes[i].selected = false
            }
            this.selectionActive = false
        },
        selectAll(){
            this.ids = []
            
            for(let i = 0; i < this.myNotes.length; i++){
                this.myNotes[i].selected = true
                this.ids.push(this.myNotes[i]._id)
            }
            
        },
        successBulk(){
            this.snackbar = true
            this.myNotes = this.myNotes.filter( note => !this.ids.includes(note._id))
            if(this.filteredNotes.length != 0){
                this.filteredNotes = this.filteredNotes.filter( note => !this.ids.includes(note._id))
            }
            this.msg = "Notes deleted successfully"
            this.selectionActive = false
            this.ids = []
        },
        errorBulk(value){
            this.selectionActive = false
            this.ids = []
            if(value.status == 401){
                this.dialog = true,
                this.sesh_err = value.body.message
            }
            else{
                this.snackbar = true
                this.message = value.body.message
            }
        },
        async bulkDelete(){
            this.bulkDialog = true
        },
        select(note){
            this.selectionActive = true
            note.selected = true
            this.ids.push(note._id)
        },
        deselect(note){
            note.selected = false
            this.ids.splice(this.ids.indexOf(note._id), 1)
            this.selectionActive = this.ids.length == 0 ? false : true
        },
        errorUpdating(value){
            if(value.status == 401){
                this.dialog = true,
                this.sesh_err = value.body.message
            }
            else{
                this.snackbar = true
                this.message = value.body.message
            }
        },
        updated(){
            this.showEditDialog = false
            this.msg = 'updated successfully'
            this.snackbar = true
        },
        btnToggleEditDialog(note){
            this.showEditDialog = true
            this.updateSelectedNote = note
        },
        btnTrashArchiveFunc: function(value){
            this.confirmDeleteDialog = true
            this.delSelectedNoteIndex = value.index
            this.delSelectedNoteId = value.note._id
            this.delSelectedNote = value.note
        },
        async btnArchive(note, index){
            console.log(note, index)

            await this.$http.post('api/setUnsetArchiveStatus/' + note._id,
            { status: note.archive },
            { headers: { Authorization: 'Bearer ' + this.$auth.getToken() } })
            .then( () => {
                this.snackbar = true
                this.msg = "Note archived"
                this.myNotes.splice(index, 1)
                note.archive = true
                this.myArchiveNotes.push(note)
            })
            .catch(err => {
                console.log(err)
            })
        },
        btnToggleColorPicker(note){
            this.showColorPickerDialog=true
            this.note_color = note.color
            this.note_colorID = note._id
            this.colorEdit_selectedNote = note
            console.log(this.note_color, this.colorEdit_selectedNote)
        },
        onSuccessNoteColor(value){
            this.snackbar = true
            this.msg = "Color updated"
            this.colorEdit_selectedNote.color = value
            this.showColorPickerDialog = false
        },
        onErrorNoteColor(value){
            this.snackbar = true
            this.msg = value
        },
         async deleteSingleTag(tag, i, note){
            console.log(tag)
            // note.tags.splice(i, 1)
            await this.$http.post('api/remove-tag/' + note._id,
            { tag: tag }, { headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then((res) => {
                this.msg = res.body.message
                this.snackbar = true
            })
            .catch( err => {
                if(err.status == 401){
                    this.dialog = true,
                    this.sesh_err = err.body.message
                }
                else{
                    this.msg = err.body.message
                    this.snackbar = true
                }
            })
            .finally( () => note.tags.splice(i, 1))
        },
        async btnUpdateExistingTag(){
            await this.$http.post('api/editNoteWithExistingLabel', { id: this.selected_noteId, tags: this.selectedLabel},
             { headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
                .then( (res) => {
                    this.snackbar = true,
                    this.msg = res.body.message
                    this.myNotes[this.selected_index].tags.splice(0, this.myNotes[this.selected_index].tags.length)
                    this.myNotes[this.selected_index].tags.push(...this.selectedLabel)
                })
                .catch( err => {
                    if(err.status == 401){
                        this.dialog = true,
                        this.sesh_err = err.body.message
                    }
                })
                .finally(() => { 
                    this.selectedLabel = []
                    this.selected_index = -1
                    this.selected_noteId = ''
                    this.addLabelDialog = false
                })
        },
        popAddLabelDialog(i, note){
            this.addLabelDialog = true
            this.selectedLabel = note.tags.slice()
            this.selected_noteId = note._id
            this.selected_index = i
        },
        async notes(){
            this.isLoading = true
            await this.$http.get('api/myNotes', { headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then( (response) => {
                this.myNotes = response.body.filter( note => {
                    return note.archive != true
                })
                this.myArchiveNotes = response.body.filter( notes => {
                    return notes.archive == true
                })
            })
            .catch( err => {
                if(err.status == 401){
                    this.dialog = true,
                    this.sesh_err = err.body.message
                }
            })
            .finally(() => {
               setTimeout( () => {
                this.isLoading = false
               }, 2500)
            })
        },
        logout(){
            this.$auth.destroyToken()
            this.$router.push('/login')
        },

        async user(){
            await this.$http.get('api/userDetails', {headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then( res => {
                this.userInfo = res.body[0]
                this.labels = res.body[0].customTags
                this.c_tag = res.body[0].customTags
            })
            .catch( err => {
                console.log(err)
            })
        },


        async btnDeleteLabel(label, i){
            console.log(label, i)
            await this.$http.post('api/deleteLabel', { tag: label }, {headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then( () => {
                this.msg = "tag deleted"
                this.snackbar = true
            })
            .catch( err => {
                this.msg = err.body.message
                this.snackbar = true
            })
            .finally( () => { 
                this.labels.splice(i, 1)
                this.notes()
            })
        },


        labelToggler(label, i){
            this.oldLabelValue = label
            this.editLabelValue = this.oldLabelValue
            this.labelEditMode = true
            this.editedIndex = i

            console.log(this.oldLabelValue)
        },

        async updateLabel(i){
            console.log(this.oldLabelValue, this.editLabelValue)
            if(this.oldLabelValue === this.editLabelValue){
                this.snackbar = true
                this.msg = 'updated'
            }
            else{
                await this.$http.post('api/update-tag', { oldTag: this.oldLabelValue, tag: this.editLabelValue }, {headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
                .then( res => {
                    this.snackbar = true
                    this.msg = res.body.message
                })
                .catch( err => {
                    this.snackbar = true
                    this.msg = err.body.message
                })
                .finally( () => {
                    this.labels[i] = this.editLabelValue
                    this.labelEditMode = false
                    this.notes()
                })
            }
        },

        async onAddLabelClick(){
            await this.$http.post('api/addCustomTag', { tag: this.addLabel }, {headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then( response => {
                this.labels.push(response.body.tag)
            })
            .catch(err => console.log(err))
            .finally(() => { this.addLabel = ''})
        },


        async onClickToggleDarkMode(){
            this.$vuetify.theme.dark = !this.$vuetify.theme.dark;

            await this.$http.post('api/darkModeToggler', { val: this.darkModeSwitch }, {headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then( res => {
                localStorage.removeItem('user-options')
                localStorage.setItem('user-options', JSON.stringify(res.body.option))
            })
            .catch( err => console.log(err))
            .finally( () => {
                this.checkUserSetting()        
            })
        },


        checkUserSetting(){
            const userOptions = JSON.parse(localStorage.getItem('user-options'))
            // console.log(userOptions.darkmode)
            if(userOptions.darkmode == true){
                this.$vuetify.theme.dark = true
                this.darkModeSwitch = true
            }
            else{
                this.$vuetify.theme.dark = false
            }

            if(userOptions.listView == true){
                this.listView = true
            }
            else{
                this.listView = false
            }
        },


        deleteSingleNote(note, index){
            // console.log(note, index)
            this.delSelectedNoteIndex = index
            this.delSelectedNoteId = note._id
            this.confirmDeleteDialog = true
            this.delSelectedNote = note
        },


        async btnConfirmSingleDelete(){
            await this.$http.post('api/deleteNote', { note_id: this.delSelectedNoteId },
            {
                headers:{
                    Authorization: 'Bearer ' + this.$auth.getToken()
                }
            })
            .then(( res ) => {
                this.snackbar = true
                this.msg = res.body
            })
            .catch( err => {console.log(err)})
            .finally(() => {
                this.delSelectedNoteId = null
                this.confirmDeleteDialog = false
                if(this.delSelectedNote.archive == true){
                    this.myArchiveNotes.splice(this.delSelectedNoteIndex, 1)
                }
                else{
                    this.myNotes.splice(this.delSelectedNoteIndex, 1)
                }
            })
        },


        async btnAddNote(){
            await this.$http.post('api/createNote', this.newNote,
            {
                headers: {
                    Authorization: 'Bearer ' + this.$auth.getToken()
                }
            })
            .then( res => {
                this.myNotes.unshift(res.body.data)
                this.snackbar = true
                this.msg = res.body.message
                this.typingMode = false
            })
            .catch( err => {
                this.snackbar = true
                this.msg = err.body.message
            })
            .finally( () => { this.newNote = {}})
        },


        onClickFilter(label){
            this.archiveStatus = false
            this.tempNotes = this.myNotes
            this.filtering = true
            this.filteredNotes = this.tempNotes.filter( notes => {
                return (notes.tags.indexOf(label) >= 0)
            })
        },


        async onClickListToggler(val){
            this.listView = val
            console.log(val)

            await this.$http.post('api/listToggler', { val: val }, {headers: { Authorization: 'Bearer ' + this.$auth.getToken() }})
            .then( res => {
                localStorage.removeItem('user-options')
                localStorage.setItem('user-options', JSON.stringify(res.body.option))
            })
            .catch( err => console.log(err))
            .finally( () => {
                this.checkUserSetting()        
            })
        },

        removeFilters(){
            this.filtering = false
            this.archiveStatus = false
        }
    },

    mounted(){
        this.notes()
        this.user()
        this.checkUserSetting()
    }
}
</script>

<style scoped>

    .no_notes{
        height: 70vh;
        color: gray;
        font-size: 45px;
    }
    .border{
        border: 0.5px solid lightgray;
    }

    .floating{
        position: fixed;
        bottom: 30px;
        right: 30px;
        z-index: 99;
        transition: 0.4s;
    }
    .pointer{
        cursor: pointer;
    }

</style>